<template>
  <div class="page-approval">
    <!-- <van-nav-bar title="审核审批" @click-left="onClickNavBarLeft" :fixed="true" /> -->
    <div class="main-content">
      <div class="tabs-wrapper">
        <van-tabs
          v-model="activeIndex"
          sticky
          swipeable
          animated
          :offset-top="0"
          title-active-color="#456fe4"
          color="#456fe4"
          @change="tabsChange"
        >
          <van-tab title="未审核">
            <div class="un-approval-content">
              <van-pull-refresh v-model="isRefreshing" @refresh="onRefresh">
                <van-list
                  class="list"
                  v-model="isLoading"
                  :finished="loadFinished"
                  :immediate-check="false"
                  :offset="offset"
                  :error.sync="error"
                  error-text="请求失败，点击重新加载"
                  finished-text=""
                  @load="onLoad"
                >
                  <div v-if="unApprovalList.length > 0">
                    <div
                      v-for="(item, index) in unApprovalList"
                      :key="index + '-' + item.id + item.documentNumber"
                      class="item-content"
                      @click="unApprovalItemClick(item)"
                    >
                      <div class="name-db-time">
                        <span class="name">{{ item.offenderName }}</span>
                        <div class="db">
                          <!-- <span v-for="(dbName, index) in item.baseInfo.db_name" :key="index">
                            <van-tag type="danger"> {{ dbName }}</van-tag>
                          </span> -->
                        </div>
                        <span class="time">{{ item.violationTime | datetime }}</span>
                      </div>
                      <div class="field">
                        <span class="key">{{ item.documentType }}：</span>
                        <span class="value">{{ item.documentNumber }}</span>
                      </div>
                      <div class="field">
                        <span class="key">违章类型：</span>
                        <span class="value">{{ item.violationType }}</span>
                      </div>
                      <div class="field">
                        <span class="key">违法行为：</span>
                        <span class="value">{{ item.violationAction }}</span>
                      </div>
                      <div class="field">
                        <span class="key">处罚人：</span>
                        <span class="value">{{ item.punishMan }}</span>
                      </div>
                      <van-divider :style="{ color: '#bfbfbf', borderColor: '#bfbfbf', padding: '0 0' }" />
                    </div>
                  </div>
                  <div v-else class="empty">空空如也...</div>
                </van-list>
              </van-pull-refresh>
            </div>
          </van-tab>
          <van-tab title="已审核">
            <div class="approval-content">
              <van-pull-refresh v-model="isRefreshing" @refresh="onRefresh">
                <van-list
                  class="list"
                  v-model="isLoading"
                  :finished="loadFinished"
                  :immediate-check="false"
                  :offset="offset"
                  :error.sync="error"
                  error-text="请求失败，点击重新加载"
                  finished-text=""
                  @load="onLoad"
                >
                  <div v-if="approvalList.length > 0">
                    <div
                      v-for="(item, index) in approvalList"
                      :key="index + '-' + item.id + item.documentNumber"
                      class="item-content"
                      @click="approvalItemClick(item)"
                    >
                      <div class="name-db-time">
                        <span class="name">{{ item.offenderName }}</span>
                        <div class="db">
                          <!-- <span v-for="(dbName, index) in item.baseInfo.db_name" :key="index">
                            <van-tag type="danger"> {{ dbName }}</van-tag>
                          </span> -->
                        </div>
                        <span class="time">{{ item.violationTime | datetime }}</span>
                      </div>
                      <div class="field">
                        <span class="key">{{ item.documentType }}：</span>
                        <span class="value">{{ item.documentNumber }}</span>
                      </div>
                      <div class="field">
                        <span class="key">违章类型：</span>
                        <span class="value">{{ item.violationType }}</span>
                      </div>
                      <div class="field">
                        <span class="key">违法行为：</span>
                        <span class="value">{{ item.violationAction }}</span>
                      </div>
                      <div class="field">
                        <span class="key">处罚人：</span>
                        <span class="value">{{ item.punishMan }}</span>
                      </div>
                      <van-divider :style="{ color: '#bfbfbf', borderColor: '#bfbfbf', padding: '0 0' }" />
                    </div>
                  </div>
                  <div v-else class="empty">空空如也...</div>
                </van-list>
              </van-pull-refresh>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import _ from 'underscore'
import qs from 'querystring'
import { mapState, mapActions } from 'vuex'
import { Toast, Tabs, Tab, PullRefresh, List, Divider, Tag } from 'vant'
import request from '@/utils/request'

Vue.use(Toast)
  .use(Tabs)
  .use(Tab)
  .use(PullRefresh)
  .use(List)
  .use(Divider)
  .use(Tag)

export default {
  name: 'approval',
  components: {},
  data() {
    return {
      activeIndex: 0,
      //
      isRefreshing: false, // 下拉刷新
      //
      isLoading: false, // 上拉加载
      error: false,
      loadFinished: false, // 上拉加载完毕
      offset: -490, // 滚动条与底部距离小于 offset 时触发load事件
      pageSize: 10, // 每页条数
      pageIndex: 1, // 页码
      //
      approvalList: [], // 审核集合
      unApprovalList: [] // 未审核集合
    }
  },

  created() {
    this.getDatas()
  },
  mounted() {},
  methods: {
    // 未审核item点击
    unApprovalItemClick(item) {
      this.$router.push({
        name: 'approval-detail',
        params: {
          id: item.id
        }
      })
    },
    // 审核item点击
    approvalItemClick(item) {
      this.$router.push({
        name: 'approval-detail',
        params: {
          id: item.id
        }
      })
    },
    // 标签切换时
    tabsChange(name, title) {
      // name:1 title:"已审核"
      this.onRefresh()
    },
    // 下拉刷新
    onRefresh() {
      this.unApprovalList = []
      this.approvalList = []

      this.pageIndex = 1
      this.loadFinished = false // 不写这句会导致你上拉到底过后在下拉刷新将不能触发下拉加载事件
      this.getDatas() // 加载数据
    },
    // 下拉加载
    onLoad() {
      this.pageIndex++
      this.getDatas()
    },
    // 获取数据
    getDatas() {
      const _self = this

      const params = {
        pageNo: _self.pageIndex,
        pageSize: _self.pageSize,
        approveCode: _self.activeIndex
      }
      request(`/api/tPunishment/getFujingPunishments?${qs.stringify(params)}`, {
        method: 'POST'
      })
        .then(response => {
          if (response.resultCode === 200) {
            const rows = response.data.records
            if (rows == null || rows.length === 0) {
              // 加载结束
              _self.loadFinished = true
              return
            }
            if (rows.length < _self.pageSize) {
              // 最后一页不足10条的情况
              _self.loadFinished = true
            }
            // 处理数据
            if (_self.activeIndex === 0) {
              if (_self.pageIndex === 1) {
                _self.unApprovalList = rows
              } else {
                _self.unApprovalList = _self.unApprovalList.concat(rows)
              }
            } else {
              if (_self.pageIndex === 1) {
                _self.approvalList = rows
              } else {
                _self.approvalList = _self.approvalList.concat(rows)
              }
            }
          } else {
            _self.loadFinished = true
          }
        })
        .catch(() => {
          this.error = true
        })
        .finally(() => {
          _self.isRefreshing = false
          _self.isLoading = false
        })
    }
  }
}
</script>

